.App {
	height: 100vh;
	width: 100vw;
	background-image: url("./assets/profile-image.jpg");
	filter: grayscale(100%);
	background-size: contain;
	background-repeat: no-repeat;
	background-position-x: center;
	background-position-y: center;
	animation: 1s ease-out 0s 1 fadeIn;
	overflow: hidden;
}

.App-inner {
	height: 100vh;
	width: 100vw;
	background: radial-gradient(
		circle,
		rgba(255, 255, 255, 0.00001) 0%,
		rgb(187, 174, 149) 35%
	);
	background: -webkit-linear-gradient(
		circle,
		rgba(255, 255, 255, 0.00001) 0%,
		rgb(187, 174, 149) 35%
	);
}

.App-inner-inner {
	height: 100vh;
	width: 100vw;
	background: linear-gradient(
		90deg,
		rgba(187, 174, 149, 0) 48%,
		rgba(187, 174, 149, 1) 52%
	);
	background: -webkit-linear-gradient(
		left,
		rgba(187, 174, 149, 0) 48%,
		rgba(187, 174, 149, 1) 52%
	);
	animation: 2s ease-out 0s 1 fadeIn;
}

.divider {
	height: 1px;
	background-color: rgb(63, 59, 52);
	width: 70%;
}

.app-bar {
	background-color: rgb(187, 174, 149);
	width: 100vw;
	height: 70px;
	border-bottom-width: 1px;
	border-bottom-color: rgb(187, 161, 112);
	border-bottom-style: solid;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	padding: 16px 16px;
	animation: 1s ease-out 0s 1 fadeIn;
}

.app-bar > h1 {
	color: rgb(63, 59, 52);
	font-size: 2rem;
}

.app-bar-nav {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}

.app-bar-nav:last-child {
	padding-right: 0px;
}

.app-bar-nav-item {
	cursor: pointer;
	background-color: transparent;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding-left: 8px;
	padding-right: 8px;
	transition: background-color 200ms linear;
	border-radius: 4px;
	color: rgb(63, 59, 52);
}

.app-bar-nav-item:hover {
	background-color: rgba(63, 59, 52, 0.07);
}

.app-bar-nav-item > .nav-image {
	width: 30px;
	height: 25px;
	color: rgb(63, 59, 52);
}

.home {
	width: 100vw;
	height: calc(100vh - 70px);
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	color: rgb(63, 59, 52);
}

.home-grid-item {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.home-welcome {
	width: 70%;
	font-size: 2.5rem;
	font-weight: bold;
	text-align: center;
}

.home-description {
	width: 60%;
	font-size: 1.1rem;
	font-weight: 200;
	text-align: center;
	animation: 2s ease-out 0s 1 fadeIn;
}

.example-enter {
	opacity: 0.01;
}

.example-enter.example-enter-active {
	opacity: 1;
	transition: opacity 500ms ease-in;
}

.example-exit {
	opacity: 1;
}

.example-exit.example-exit-active {
	opacity: 0.01;
	transition: opacity 300ms ease-in;
}

@keyframes fadeIn {
	0% {
		opacity: 0%;
	}
	100% {
		opacity: 100%;
	}
}

@keyframes translateInFromRight {
	0% {
		transform: translateX(500px);
	}
	50% {
		transform: translateX(500px);
	}
	100% {
		transform: translateX(0);
	}
}
